import axios from "axios";
import React, { ChangeEvent, Component } from "react";
import { connect } from "react-redux";
import { Auth, Basic, FC_GetAllTermination } from "../../actions";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Loading from "../../components/Loading/Loading";
import { APP_URL } from "../../config/app.config";
import { StoreState } from "../../reducers";
import { setAxiosToken } from "../../utils/AxiosToken";
import { errorToText } from "../../utils/errors";

enum FormErrorTypes {
  MAIN = "MAIN",
  CURRENT_PASSWORD = "CURRENT_PASSWORD",
  NEW_PASSWORD = "NEW_PASSWORD",
  CONFIRM_PASSWORD = "CONFIRM_PASSWORD",
}

interface ChangePasswordProps {
  auth: Auth;
}
interface ChangePasswordState {
  loading: boolean;
  current_password: string;
  new_password: string;
  confirm_password: string;
  formError: { target: FormErrorTypes; msg: string } | null;
  success: string;
}

export class _ChangePassword extends Component<
  ChangePasswordProps,
  ChangePasswordState
> {
  constructor(props: ChangePasswordProps) {
    super(props);
    this.state = {
      loading: false,
      current_password: "",
      new_password: "",
      confirm_password: "",
      formError: null,
      success: "",
    };
  }
  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };
  Fn_ChangePassword = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (this.state.current_password === "") {
      return this.setState({
        formError: {
          target: FormErrorTypes.CURRENT_PASSWORD,
          msg: "Please fill current or existing password",
        },
      });
    }
    if (this.state.new_password === "") {
      return this.setState({
        formError: {
          target: FormErrorTypes.NEW_PASSWORD,
          msg: "Please fill new password",
        },
      });
    }
    if (this.state.new_password.length < 5) {
      return this.setState({
        formError: {
          target: FormErrorTypes.NEW_PASSWORD,
          msg: "Please password characters should not be less than 5",
        },
      });
    }
    if (this.state.confirm_password === "") {
      return this.setState({
        formError: {
          target: FormErrorTypes.CONFIRM_PASSWORD,
          msg: "Please confirm your new password",
        },
      });
    }
    if (this.state.confirm_password !== this.state.new_password) {
      return this.setState({
        formError: {
          target: FormErrorTypes.CONFIRM_PASSWORD,
          msg: "Password does not match with new password",
        },
      });
    }
    if (this.props.auth.user !== null) {
      let object_request = {
        user_id: this.props.auth.user?.user_id,
        current_password: this.state.current_password,
        new_password: this.state.new_password,
      };
      this.setLoading(true);
      setAxiosToken();
      try {
        const res = await axios.post(
          `${APP_URL}/changepassword/new`,
          object_request
        );
        console.log("status: ", res.status);
        if (res) {
          this.setState({
            current_password: "",
            new_password: "",
            confirm_password: "",
            formError: null,
            success: "Password changed successfully!",
          });
        }
        this.setLoading(false);
      } catch (error: any) {
        this.setState({
          formError: {
            target: FormErrorTypes.MAIN,
            msg: errorToText(error),
          },
          success: "",
          loading: false,
        });
      }
    }
  };

  render() {
    if (this.state.loading === true) {
      return (
        <div className="bg-white rounded-md p-4 flex flex-col items-center justify-center m-5">
          <Loading />
          <div className="text-xl">Loading, please wait...</div>
        </div>
      );
    }
    return (
      <div>
        <DashboardContainer>
          <div className="flex flex-col">
            <span className="text-3xl font-bold -mb-1">Change password</span>
            <span className="text-primary-800 font-bold">
              {this.props.auth.user?.full_name}
            </span>
          </div>
          {this.state.success !== "" && (
            <div
              onClick={() => this.setState({ success: "" })}
              className="bg-green-100 text-green-800 rounded px-3 py-2 cursor-pointer"
            >
              {this.state.success}
            </div>
          )}
          {this.state.formError !== null &&
            this.state.formError.target === FormErrorTypes.MAIN && (
              <div
                onClick={() => this.setState({ formError: null })}
                className="bg-red-100 text-red-800 rounded px-3 py-2 cursor-pointer"
              >
                {this.state.formError.msg}
              </div>
            )}
          <div className="mt-5">
            <form onSubmit={this.Fn_ChangePassword}>
              <div className="flex flex-col">
                <label className="text-base" htmlFor="">
                  Current password
                </label>
                <input
                  type="password"
                  value={this.state.current_password}
                  onChange={(e) =>
                    this.setState({
                      formError: null,
                      current_password: e.target.value,
                    })
                  }
                  className={`border ${
                    this.state.formError?.target ===
                    FormErrorTypes.CURRENT_PASSWORD
                      ? "border-red-600 animate__animated animate__shakeX animate__faster"
                      : "border-gray-400"
                  }  rounded px-3 py-2`}
                />
                {this.state.formError?.target ===
                  FormErrorTypes.CURRENT_PASSWORD && (
                  <span className="text-red-600 font-bold">
                    {this.state.formError.msg}
                  </span>
                )}
              </div>
              <div className="flex flex-col mt-3">
                <label className="text-base" htmlFor="">
                  New password
                </label>
                <input
                  type="password"
                  value={this.state.new_password}
                  onChange={(e) =>
                    this.setState({
                      formError: null,
                      new_password: e.target.value,
                    })
                  }
                  className={`border ${
                    this.state.formError?.target === FormErrorTypes.NEW_PASSWORD
                      ? "border-red-600 animate__animated animate__shakeX animate__faster"
                      : "border-gray-400"
                  }  rounded px-3 py-2`}
                />
                {this.state.formError?.target ===
                  FormErrorTypes.NEW_PASSWORD && (
                  <span className="text-red-600 font-bold">
                    {this.state.formError.msg}
                  </span>
                )}
              </div>
              <div className="flex flex-col mt-3">
                <label className="text-base" htmlFor="">
                  Confirm password
                </label>
                <input
                  type="password"
                  value={this.state.confirm_password}
                  onChange={(e) =>
                    this.setState({
                      formError: null,
                      confirm_password: e.target.value,
                    })
                  }
                  className={`border ${
                    this.state.formError?.target ===
                    FormErrorTypes.CONFIRM_PASSWORD
                      ? "border-red-600 animate__animated animate__shakeX animate__faster"
                      : "border-gray-400"
                  }  rounded px-3 py-2`}
                />
                {this.state.formError?.target ===
                  FormErrorTypes.CONFIRM_PASSWORD && (
                  <span className="text-red-600 font-bold">
                    {this.state.formError.msg}
                  </span>
                )}
              </div>
              <div className="my-4">
                <button
                  className="cursor-pointer bg-primary-800 text-white rounded px-3 py-2 hover:bg-blue-900"
                  type="submit"
                >
                  Change password
                </button>
              </div>
            </form>
          </div>
        </DashboardContainer>
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
}: StoreState): {
  auth: Auth;
} => {
  return { auth: auth };
};

export const ChangePassword = connect(mapStateToProps, {})(_ChangePassword);
